@media screen and (max-width: 767px) {
  .program {
    padding-top: 28px;
    padding-bottom: 28px;

    &__title {
      margin-bottom: 8px;

      font-family: Montserrat Alternates, sans-serif;
      font-weight: 500;
      font-size: 24px;
      line-height: 1.22;
      color: var(--primary-color-text);
      text-align: center;
    }

    &__text,
    &__text-hidden {
      font-weight: 400;
      font-size: 16px;
      line-height: 1.25;
      color: var(--primary-color-text);
      margin-bottom: 10px;
    }

    &__text-hidden {
      margin-bottom: 16px;
    }

    &__items {
      margin-bottom: 16px;
      padding-left: 32px;
    }

    &__item:not(:last-child) {
      margin-bottom: 8px;
    }

    &__item {
      position: relative;
      display: inline-flex;
      align-items: center;
      margin-bottom: 10px;
      font-weight: 400;
      font-size: 16px;
      line-height: 1.25;
      color: var(--primary-color-text);
    }

    &__item::before {
      position: absolute;
      left: -32px;
      content: '';
      display: inline-block;
      width: 24px;
      height: 24px;

      background-size: contain;
      background-image: url(../images/icons/Іко.svg);
      background-repeat: no-repeat;
    }

    #invisible-text-program {
      display: none;
    }

    #btn-program {
      background-color: transparent;
      border: transparent;
      outline: transparent;

      font-weight: 500;
      font-size: 16px;
      line-height: 1.25;
      color: var(--primary-color-text);
    }
  }

  .wrap-pic {
    display: flex;
    justify-content: center;
    margin-top: 24px;

    &__column:last-child {
      margin-top: 20px;
      margin-left: 20px;
    }

    &__item:first-child {
      margin-bottom: 28px;
    }

    &__inner {
      position: relative;
      width: 130px;
      height: 130px;
      border-radius: 50%;
      background: linear-gradient(
        0.12deg,
        #000000 -11.45%,
        rgba(156, 156, 156, 0) 69.04%
      );
    }

    &__img {
      border-radius: 50%;
      border: 1px solid var(--primary-bg-button);
    }

    &__position {
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background: linear-gradient(
        0.12deg,
        #000000 -11.45%,
        rgba(156, 156, 156, 0) 69.04%
      );
    }

    &__subtitle {
      margin-top: 62px;
      margin-bottom: 8px;

      font-family: Montserrat Alternates, sans-serif;
      font-weight: 500;
      font-size: 14px;
      line-height: 1.22;
      color: var(--picture-color-text);
      text-align: center;
    }

    &__text {
      margin-bottom: 28px;

      font-weight: 400;
      font-size: 12px;
      line-height: 1.25;
      color: var(--picture-color-text);
      text-align: center;
    }
  }
}

